import React, { FC, Fragment } from "react";
import "./styles.scss";
import Buttons from "../Buttons/Buttons";
import {
  formatDateIntlDateTime,
  formatDateIntlTimeDate,
} from "../UI/functions/functions";
import icons from "../../assets/icons/icons";
import { RouteNames } from "../../routes";
import { useNavigate } from "react-router-dom";
import JobApiRequest from "../../api/Job/Job";

const MyVacancyСard: FC = ({
  //@ts-ignore
  dataCard,
  //@ts-ignore
  onClick,
  //@ts-ignore
  modal,
  //@ts-ignore
  onRead,
  //@ts-ignore
  onArchive,
  //@ts-ignore
  onApplicantFound,
  //@ts-ignore
  archive,
}) => {
  const formatCurrency = (value: string) =>
    new Intl.NumberFormat("ru", {
      style: "currency",
      currency: "RUB",
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    }).format(parseFloat(value));

  const navigate = useNavigate();

  return (
    <div
      id={dataCard.id}
      key={dataCard.id}
      className={`cardContainer ${modal && "modalActive"}`}
    >
      <div className="cardContainerBody">
        <div className="cardHeaderTag">
          <p className="cardTag__type">{dataCard.job_type}</p>
          <p className="cardTag">{dataCard.job_category}</p>
          <p className="cardTag">{dataCard.job_activity}</p>
        </div>
        <div className="cardTitleContainer">
          <h4 className="cardTitle">{dataCard.title}</h4>
          <h5 className="cardPrice">{`${formatCurrency(
            dataCard.salary_min
          )} - ${formatCurrency(dataCard.salary_max)}`}</h5>
        </div>
        <p className="cardDescription">{dataCard.description}</p>
        {dataCard?.user && (
          <div className="containerResponses">
            <Fragment>
              {dataCard?.user
                ?.map((item: any) => {
                  return (
                    <img
                      src={item.avatars}
                      key={item.id}
                      className="responsesAvatarts"
                    ></img>
                  );
                })
                .reverse()}
              {dataCard?.user?.length > 8 && (
                <p className="numberResponses">{`+${
                  dataCard.user.length - 8
                }`}</p>
              )}
            </Fragment>
          </div>
        )}
      </div>

      {!archive && (
        <div className="footerCard">
          <div className="buttonContainer">
            <Buttons
              ico={icons.edit}
              text={"Редактировать"}
              onClick={() =>
                navigate(RouteNames.VACANCYCREATE, {
                  state: { idJob: dataCard.id, type: "redact" },
                })
              }
              className="whiteButtonCard"
            />
            <Buttons
              ico={icons.file}
              text={"Убрать в архив"}
              //@ts-ignore
              onClick={() => onArchive(dataCard.id)}
              className="whiteButtonCard"
            />
            <Buttons
              ico={icons.userCheck}
              text={"Сотрудник найден"}
              //@ts-ignore
              onClick={() => {
                onApplicantFound(dataCard.id);
              }}
              className="orangeButtonCard"
            />
          </div>
        </div>
      )}
    </div>
  );
};

export default MyVacancyСard;
