import React, { FC, Fragment, useState } from "react";
import "./styles.scss";
import FormInput from "../FormInput/FormInput";
import Buttons from "../Buttons/Buttons";
import ToggleButton from "../ToggleButton/ToggleButton";

const SearchComponent: FC = ({ onSearch, dataCategory, onToggle }: any) => {
  const [isText, setIsText] = useState<string>();

  return (
    <Fragment>
      <div className="containerSearch">
        <div className="searchContainerInput">
          <FormInput
            style={"searchInput"}
            placeholder="Какую работу вы ищете?"
            value={undefined}
            onChange={(e) => {
              setIsText(e);
            }}
            subInput={undefined}
            required={false}
            error={""}
            keyData={""}
          ></FormInput>
          <ToggleButton
            onToggle={(e) => {
              onToggle(e);
            }}
            text={"Ищу стажировку"}
            formDataKey={""}
            checked={undefined}
            styleContainer={"toggleSearch"}
          />
        </div>
        <Buttons
          text={"Найти"}
          onClick={() => {
            onSearch(isText);
          }}
          className="btnOrange"
        />
      </div>
      {dataCategory && dataCategory.length != 0 && (
        <div className="categoryContainer">
          {dataCategory?.map((item: any) => {
            return (
              <div key={item.id} className="categoryItem">
                {item.text}
              </div>
            );
          })}
        </div>
      )}
    </Fragment>
  );
};

export default SearchComponent;
