import React, { FC, Fragment, useEffect } from "react";
import "./styles.scss";
import Header from "../../components/Header/Header";
import Footer from "../../components/Footer/Footer";
import Buttons from "../../components/Buttons/Buttons";
import { useNavigate } from "react-router-dom";
import { RouteNames } from "../../routes";
import icons from "../../assets/icons/icons";
import ProfileComponents from "../../components/ProfileComponents/ProfileComponents";
import { useDispatch } from "react-redux";
import { handleLogin } from "../../actions/UserActions";

const Profile: FC = () => {
  const navigation = useNavigate();

  const dispatch = useDispatch();

  useEffect(() => {
    //@ts-ignore
    dispatch(handleLogin());
  }, [dispatch]);

  return (
    <Fragment>
      <section className="section">
        <Header />

        <div className="pageContainerTitle">
          <Buttons
            ico={icons.arrowLeft}
            text={"Вернутья на главную"}
            className="backButton"
            onClick={() => navigation(RouteNames.HOMEPAGE)}
          ></Buttons>
          <h1 className="titlePage">Профиль</h1>
          <h6 className="subTitlePage">Заполните данные и расскажите о себе</h6>
        </div>

        <div className="pageContainer">
          <ProfileComponents />
        </div>
      </section>
      <Footer />
    </Fragment>
  );
};

export default Profile;
